<template>
  <div id="app">
    <router-view/>

  </div>
</template>

<script>
  export default {
    name: 'App',
    methods: {
      subname(txt) {
        if (txt) {
          if (txt.length > 41) {
            return txt.substring(0, 12) + '...' + txt.substring(30, 42)
          } else {
            return txt
          }
        }
      },
      load(){
        setTimeout(()=>{
          this.load()
        },10000)
        this.$axios.post('https://json.hens.domains/txmint', {
          'key': '',
          'num': 1
        }).then(res => {
          let sum = Math.ceil(Math.random() * res.data.list.length)
          if (res.data.list[sum]) {
            this.$notify({
              title: "Mintï¼š"+res.data.list[sum].name,
              message: this.subname(res.data.list[sum].addr),
              position: 'bottom-left',
            })
          }
        })
      }
    },
    mounted() {
      this.load()
    }
  }
</script>

<style>
  .el-message-box {
